	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"All Systems Go","date":"25 Feb 2002","slug":"/2002-02-25-all-systems-go/","tags":"Y2002"},"html":"<p>My folks have finalized their plans to meet us in Las Vegas the week of April 21. They'll be able to have as much of Christopher as they can handle!</p>\n<p>I'm trying out <a href=\"http://brentashley.manilasites.com/2002/02/24\">BlogChat</a>, which is not only a wonderful way to waste time chatting, but might actually have some practical use (chatting with my parents, or with Mama upstairs when we're on our respective PCs). Here's the link... I'll try it out this evening (German time), so it it says I'm online, you can try it out:</p>\n<p><a href=\"#\" title=\"you can chat with me if i'm in\" onclick=\"window.open('http://www.papascott.de/blogchat/anybodyhome.php','','height=230,width=355,toolbar=no,location=no,resizable=yes');return false;\"><img src=\"http://www.papascott.de/blogchat/thedoctoris.php\" alt=\"you can chat with me if i'm in\" border=\"0\" /></a></p>"},"next":{"fields":{"title":"Progress Report","date":"24 Feb 2002","slug":"/2002-02-24-progress-report/"}},"previous":{"fields":{"title":"Transrapid Money","date":"25 Feb 2002","slug":"/2002-02-25-transrapid-money/"}}},{"node":{"fields":{"title":"Progress Report","date":"24 Feb 2002","slug":"/2002-02-24-progress-report/","tags":"Y2002"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/02/crhbathfeb02.jpg\" height=\"325\" width=\"244\" border=\"0\" alt=\"crhbathfeb02.jpg: \" /></p>\n<p>Milestone check for Christopher:\n<ul>\n<li>We recognized his first sentence yesterday, \"Nana essen\" (Eat banana), which of course has to do with eating. Earlier in the week he said \"Mama mehr\" (Mama more), which was disqualified for not having a verb.\n</li>\n<li>This morning for the first time he seemed content to have his bath in the big bathtub, rather than the plastic baby tub. He's still not really having fun, but we figure he just needs some new tub toys.\n</li>\n<li>He's able to start a CD player by himself, but cannot distinguish among music CDs, data CDs, and DVDs (then again, who can without reading the labels?). He got very upset the other day when he couldn't listen to Shrek.\n</li>\n<li>Today he was able to climb onto the big slide at the McDonald's indoor playland by himself. It was almost impossible to drag him away.\n</li>\n<li>He's right in the middle of the terrible twos, what Germans call the \"Trotzphase\", testing our authority and our limits. I gets loud sometimes.\n</li>\n</ul>"},"next":{"fields":{"title":"Does This Stuff Come Off?","date":"23 Feb 2002","slug":"/2002-02-23-does-this-stuff-come-off/"}},"previous":{"fields":{"title":"All Systems Go","date":"25 Feb 2002","slug":"/2002-02-25-all-systems-go/"}}},{"node":{"fields":{"title":"Does This Stuff Come Off?","date":"23 Feb 2002","slug":"/2002-02-23-does-this-stuff-come-off/","tags":"Y2002"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/02/smhmakeup.jpg\" height=\"257\" width=\"325\" border=\"0\" alt=\"smhmakeup.jpg: \" /></p>\n<p>No, this isn't how I prepare for the televised Olympic coverage. We were invited to a 'Fasching' birthday party. I wasn't aware that we would be dressing up until the last minute, and this is what I came up with on short notice.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/02/fphmakeup.jpg\" height=\"244\" width=\"325\" border=\"0\" alt=\"fphmakeup.jpg: \" /></p>\n<p><a href=\"http://moorbek.weblogs.com/\">Moorbek</a> had some intresting (albeit cynical) thoughts on the German Olympic success, which I'll loosely translate: \"We have two winter sports regions: the Alps and the Walachei in Thürigen (you can't really count the Harz or the Black Forest). There live roughly 80,000 people. The German winter athletes are recruited from these 80,000 people, not from the 80 million German population. The succeses were all in endurance and strength disciplines (cross-country, speed skating). In the technical contests (alpine skiing, ice hockey, figure skating) the results were as usual. In cycling (Ullrich, Zabel), I had always wondered how long the DDR doping advantage would last. This was the wrong question, of course. The question should be, how do the trainers of today keep their athletes from being tested positive? The science of sport learned from German reunification. No proof, of course, but one can't help but notice.\"</p>"},"next":{"fields":{"title":"Jagoda To Go","date":"22 Feb 2002","slug":"/2002-02-22-jagoda-to-go/"}},"previous":{"fields":{"title":"Progress Report","date":"24 Feb 2002","slug":"/2002-02-24-progress-report/"}}}],"pathPrefix":"","first":false,"last":false,"index":922,"pageCount":1101}}

